import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";
import {
  getMiniCounters,
  getMiniVehicles,
} from "../../../../../../store/mini/miniSlice";
import {
  isDrawerVisible,
  InsurerFilterInputChangeValue,
  getInsurerList,
  setInsurerParams,
} from "../../../../../../store/sideMenu/newInsurance/newInsuranceSlice";
import {
  getVehicles,
  SetVehiclesParams,
} from "../../../../../../store/Masters/Vehicles/vehiclesSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {
    props.getMiniCounters();
    props.getMiniVehicles();
  }, []);
  const onFinish = () => {
    props.setInsurerParams({
      ...props.insurerparams,
      start_date: props.insurerparams.start_date,
      end_date: props.insurerparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getInsurerList({
      ...props.insurerparams,
      // start_date:
      //   props.insurerparams.start_date != null &&
      //   props.insurerparams.start_date != ""
      //     ? moment(props.insurerparams.start_date).format("YYYY-MM-DD")
      //     : "",
      // end_date:
      //   props.insurerparams.end_date != null &&
      //   props.insurerparams.end_date != ""
      //     ? moment(props.insurerparams.end_date)
      //         .add(1, "days")
      //         .format("YYYY-MM-DD")
      //     : "",
      start_date: props.insurerparams.start_date,
      end_date: props.insurerparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isDrawerVisible(false);
  };

  function clearFilter() {
    props.setInsurerParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getInsurerList({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getMiniCounters();
    props.getMiniVehicles();
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  const insuranceChoice = [
    { id: 1, name: "1st Party" },
    { id: 2, name: "2nd Party" },
    { id: 3, name: "3rd Party" },
  ];
  const insuranceStatus = [
    { id: 1, name: "Requested" },
    { id: 2, name: "Reviewed" },
    { id: 3, name: "Processed" },
    { id: 4, name: "Rejected" },
  ];

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.insurerparams?.start_date,
      end_date: props.insurerparams?.end_date,
      counter: props.insurerparams?.counter,
      insurancecategory: props.insurerparams?.insurancecategory,
      insurancestatus: props.insurerparams?.insurancestatus,
      vehicle: props.insurerparams.vehicle
    });
  }, [props.insurerparams]);

  function onSearch(val) {
    props.getInsurerList({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          className="hp-drawer-mobile"
          onClose={() => {
            props.isDrawerVisible(false);
          }}
          placement="right"
          visible={props.drawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date" className="hp-mb-10">
                  <DatePicker
                    style={{ width: "100%" }}
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.InsurerFilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.InsurerFilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date" className="hp-mb-10">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(props.insurerparams?.start_date).format(
                        "YYYY-MM-DD"
                      ),
                    })}
                    disabled={
                      props.insurerparams.start_date == null ||
                      props.insurerparams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.InsurerFilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Counter" name="counter" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Counter"
                    optionFilterProp="Counter"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InsurerFilterInputChangeValue({
                        key: "counter",
                        value: value,
                      });
                    }}
                    value={
                      props.insurerparams.counter_id == null
                        ? ""
                        : props.insurerparams?.counter_id
                    }
                  >
                    {props.miniCountersList.map((counter, index) => {
                      return (
                        <Option key={index} value={counter.id}>
                          {counter.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Insurance Category" name="insurancecategory" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Insurance Category"
                    optionFilterProp="paid"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InsurerFilterInputChangeValue({
                        key: "insurancecategory",
                        value: value,
                      });
                    }}
                    value={
                      props.insurerparams.insurancecategory == null
                        ? ""
                        : props.insurerparams?.insurancecategory
                    }
                  >
                    {insuranceChoice.map((insurance, index) => {
                      return (
                        <Option key={index} value={insurance.id}>
                          {insurance.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="Vehicle" name="vehicle" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select Vehicle"
                    optionFilterProp="vehicle"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onSearch={(value) => {
                      props.SetVehiclesParams({
                        ...props.vehiclesParams,
                        page: 1,
                        search: value,
                      });
                      props.getVehicles({
                        ...props.vehiclesParams,
                        page: 1,
                        search: value,
                      });
                    }}
                    defaultActiveFirstOption={false}
                    showArrow={true}
                    onChange={(value) => {
                      props.InsurerFilterInputChangeValue({
                        key: "vehicle",
                        value: value,
                      });
                    }}
                    onPopupScroll={() => {
                      if (
                        props.vehiclesParams.page <
                        props.vehiclesParams.no_of_pages
                      ) {
                        props.SetVehiclesParams({
                          ...props.vehiclesParams,
                          page: props.vehiclesParams.page + 1,
                        });
                        props.getVehicles({
                          ...props.vehiclesParams,
                          page: props.vehiclesParams.page + 1,
                        });
                      }
                    }}
                    value={
                      props.insurerparams.vehicle == null
                        ? ""
                        : props.insurerparams?.vehicle
                    }
                  >
                    {props.miniVehiclesList.map((vehicle, index) => {
                      return (
                        <Option key={index} value={vehicle.id}>
                          {vehicle.vehicleno}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="Insurance Status" name="insurancestatus" className="hp-mb-10">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Insurance Status"
                    optionFilterProp="insurancestatus"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InsurerFilterInputChangeValue({
                        key: "insurancestatus",
                        value: value,
                      });
                    }}
                    value={
                      props.insurerparams.insurancestatus == null
                        ? ""
                        : props.insurerparams?.insurancestatus
                    }
                  >
                    {insuranceStatus.map((insurancestatus, index) => {
                      return (
                        <Option key={index} value={insurancestatus.id}>
                          {insurancestatus.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12} xs={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12} xs={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ newInsurance, mini }) {
  return {
    drawer: newInsurance.drawer,
    insurerparams: newInsurance.insurerparams,
    miniCountersList: mini.miniCountersList,
    miniVehiclesList: mini.miniVehiclesList,
  };
}

export default connect(mapStateToProps, {
  getInsurerList,
  isDrawerVisible,
  setInsurerParams,
  InsurerFilterInputChangeValue,
  getMiniCounters,
  getMiniVehicles,
  SetVehiclesParams,
  getVehicles,
})(filter);
