import { Fragment, useEffect } from "react";
import { connect } from "react-redux";
import { useHistory } from "react-router";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import {
  Button,
  DatePicker,
  Form,
  Modal,
  Row,
  Col,
  Select,
  Upload,
  Input,
} from "antd";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import moment from "moment";
import {
  isInsurerAddModelVisible,
  InsurerInputChangeValue,
  getInsurerImageUpload,
  addInsurerData,
  getByVehicleVersion,
  getVersionInsuranceMaster,
  insuranceNumberValidate
} from "../../../../../store/sideMenu/newInsurance/newInsuranceSlice";
import {
  LoadingOutlined,
  PlusOutlined,
  EditOutlined,
  InfoCircleOutlined,
} from "@ant-design/icons";
import { getMiniInsurance } from "../../../../../store/mini/miniSlice";

const addInsurer = (props) => {
  const [form] = Form.useForm();
  const history = useHistory();
  useEffect(() => {
    props.getMiniInsurance();
  }, []);

  function ChangeHandeler(e) {
    props.InsurerInputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  const beforeUpload = (file) => {
    const isJpgOrPng = file.type === "image/jpeg" || file.type === "image/png";

    if (!isJpgOrPng) {
      message.error("You can only upload JPG/PNG file!");
    }

    const isLt2M = file.size / 1024 / 1024 < 2;

    if (!isLt2M) {
      message.error("Image must smaller than 2MB!");
    }

    return isJpgOrPng && isLt2M;
  };

  const uploadButton = (
    <>
      {" "}
      <div className="hp-center hp-mt-0 ">
        {props.imageLooading ? (
          <LoadingOutlined />
        ) : (
          <PlusOutlined className="hp-mt-16" />
        )}
        <div className="hp-mt-8 hp-mb-16">Upload</div>
      </div>
    </>
  );

  function getVehicleVersion(id) {
    props.miniVehiclesList.forEach((vehicle) => {
      if (vehicle.id == id) {
        props.getByVehicleVersion(vehicle.version);
      }
    });
  }

  // Updates Country in Store
  const handleUpdate = () => {
    const insurerObj = {
      ...props.insuranceUpdateData,
      // id: id,
      expdate: moment(props.insuranceUpdateData?.expdate).format("YYYY-MM-DD"),
      date: moment().format("YYYY-MM-DD"),

    };
    props.addInsurerData({ insurerObj, id: props.insuranceData.id });
  };

  const handleResetInputValues = () => {
    props.isInsurerAddModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    return (
      <Fragment>
        <Button
          type="primary"
          onClick={() => {
            form.submit();
          }}
          block
        >
          Update
        </Button>
      </Fragment>
    );
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      Update Insurer - {props.insuranceData?.vehicle?.vehicleno}
    </h5>
  );

  // function disableDateRanges(range = { startDate: false, endDate: false }) {
  //   const { startDate, endDate } = range;
  //   return function disabledDate(current) {
  //     let startCheck = true;
  //     let endCheck = true;
  //     if (startDate) {
  //       startCheck = current && current < moment(startDate, "YYYY-MM-DD");
  //     }
  //     if (endDate) {
  //       endCheck = current && current > moment(endDate, "YYYY-MM-DD");
  //     }
  //     return (startDate && startCheck) || (endDate && endCheck);
  //   };
  // }

  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck = current && current < moment(startDate, "YYYY-MM-DD");
      }
      if (endDate) {
        endCheck = current && current > moment(endDate, "YYYY-MM-DD");
      }
      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      amount: props.insuranceUpdateData?.amount,
      expdate: props.insuranceUpdateData?.expdate,
      number: props.insuranceUpdateData?.number,
      insurancecompany_id: props.insuranceUpdateData?.insurancecompany_id,
    });
  }, [props.insuranceUpdateData]);



 

  return (
    <Modal
      visible={props.insurerAddModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isInsurerAddModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={516}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          handleUpdate();
        }}
      >
        <Row gutter={[12, 0]}>
          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label=" Amount"
              name="amount"
              rules={[
                {
                  required: true,
                  message: "Please Input  Amount",
                },
              ]}
              hasFeedback
            >
              <NumericInput
                id="amount"
                name="amount"
                style={{ width: "100%" }}
                placeholder=" Amount"
                maxLength={6}
                value={
                  props.insuranceUpdateData?.amount == null
                    ? ""
                    : props.insuranceUpdateData?.amount
                }
                onChange={props.InsurerInputChangeValue}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Expire Date  :"
              name="expdate"
              rules={[
                {
                  required: true,
                  message: "Please select your Expire Date!",
                },
              ]}
              hasFeedback
            >
              <DatePicker
                id="expdate"
                name="expdate"
                style={{ width: "100%" }}
                format="DD-MM-YYYY"
                disabledDate={disableDateRanges({
                  // endDate: moment().add(1, "months"),
                  startDate:  moment(new Date()).add(1, "years"),
                  // endDate: moment().add(1, "years")
                })}
                placeholder="Select Expire Date"
                value={
                  props.insuranceUpdateData.expdate == null
                    ? ""
                    : props.insuranceUpdateData?.expdate
                }
                onChange={(date) => {
                  props.InsurerInputChangeValue({
                    key: "expdate",
                    value: date,
                  });
                }}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            {/* <Form.Item label="Customer Mobile"
              name="number"
              rules={[
                {
                  required: true,
                  message: "Please Input Customer Mobile",
                },
                {
                  min: 10,
                  message: "Please Input Customer Mobile",
                },
              ]}
              hasFeedback>
              <NumericInput
                id="number"
                name="number"
                style={{ width: "100%" }}
                placeholder="Customer Mobile"
                maxLength={10}
                value={
                  props.insuranceUpdateData?.number == null ? "" : props.insuranceUpdateData?.number
                }
                onChange={props.InsurerInputChangeValue}
              />
            </Form.Item> */}
            <Form.Item
              label="Insurance Number"
              name="number"
              rules={[
                {
                  required: true,
                  message: "Please input your Insurance Number!",
                },
                // {
                //   pattern: new RegExp(/[A-Z]{4}[0][a-zA-Z0-9]{6}$/i),
                //   message: "field does not accept IFSC code",
                // },
              ]}
              hasFeedback
            >
              <Input
                id="number"
                name="number"
                style={{ width: "100%" }}
                maxLength={15}
                placeholder="Insurance Number"
                onBlur={(data) => {
                  // if( props.insuranceData.vehicle.id != "" &&  props.insuranceData.vehicle.id  != null && props.insuranceData.vehicle.id  !=undefined){
                    props.insuranceNumberValidate({
                      insurancenumber: data.target.value,
                      vehicle_id: props.insuranceData?.vehicle?.id
                    });
                  // }
                 
                }}
                value={props.insuranceUpdateData?.number}
                onChange={(e) => {
                  props.InsurerInputChangeValue({
                    key: e.target.name,
                    value: e.target.value.toUpperCase(),
                  });
                }}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Insurance Company"
              name="insurancecompany_id"
              rules={[
                {
                  required: true,
                  message: "Please Select Insurance Company",
                },
              ]}
              hasFeedback
            >
              <Select
                showSearch
                placeholder="Select a Insurance Company"
                optionFilterProp="Counter"
                filterOption={(input, option) =>
                  option.children.toLowerCase().includes(input.toLowerCase())
                }
                onChange={(value) => {
                  props.InsurerInputChangeValue({
                    key: "insurancecompany_id",
                    value: value,
                  });
                }}
                value={
                  props.insuranceUpdateData.insurancecompany_id == null
                    ? ""
                    : props.insuranceUpdateData?.insurancecompany_id
                }
              >
                {props.miniInsuranceList.map((insurancecompany, index) => {
                  return (
                    <Option key={index} value={insurancecompany.id}>
                      {insurancecompany.name}
                    </Option>
                  );
                })}
              </Select>
            </Form.Item>
          </Col>
        </Row>
        <Row gutter={[12, 0]}>
          <Col span={24} xs={24} md={10} xl={10} className="hp-pr-sm-0 hp-pr-12">
            <p className="hp-pb-xs-16 hp-mb-2 hp-text-color-primary-1 hp-font-weight-600">
              Insurance Image
            </p>
            <Upload
              listType="picture-card hp-w-100"
              className="avatar-uploader"
              showUploadList={false}
              accept="image/*"
              beforeUpload={beforeUpload}
              customRequest={(info) => {
                props.getInsurerImageUpload({
                  file: info.file,
                  imageType: "insuranceimage",
                });
              }}
            >
              {props.insuranceUpdateData.insuranceimage != null ? (
                <div className="hp-d-flex">
                  <img
                    className="ant-upload-select-picture-card hp-preview-img"
                    src={props.insuranceUpdateData.insuranceimage?.file}
                    alt="avatar"
                    style={{
                      width: "100%",
                    }}
                  />
                  <EditOutlined />
                </div>
              ) : (
                uploadButton
              )}
            </Upload>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ newInsurance, mini, vehicles }) {
  return {
    insuranceData: newInsurance.insuranceData,
    insuranceUpdateData: newInsurance.insuranceUpdateData,
    insurerAddModel: newInsurance.insurerAddModel,
    miniInsuranceList: mini.miniInsuranceList,
  };
}

export default connect(mapStateToProps, {
  isInsurerAddModelVisible,
  InsurerInputChangeValue,
  getMiniInsurance,
  addInsurerData,
  getInsurerImageUpload,
  insuranceNumberValidate
})(addInsurer);
