import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Spin,
  Table,
  Popconfirm,
  Space,
  Tag,
} from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  OrderedListOutlined,
  AppstoreOutlined,
  PlusOutlined,
  FilterOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import moment from "moment";
import {
  getInsurerList,
  setInsurerParams,
  isInsurerAddModelVisible,
  isDrawerVisible,
  isInsuranceViewModelVisible,
  getInsuranceById,
  addInsurerData,
  getInsurerData,
  clearInsurerList
} from "../../../../../store/sideMenu/newInsurance/newInsuranceSlice";
import Filter from "./Filters/Filter";
import AddInsurer from "./addInsurer";
import InsuranceViewModel from "../InsuranceViewModel";
import { isMobile } from "react-device-detect";

function insurer(props) {
  const { Search } = Input;
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";
  useEffect(() => {
    props.getInsurerList({
      ...props.insurerparams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  function onPageChange(page, pageSize) {
    props.clearInsurerList();
    props.setInsurerParams({
      ...props.insurerparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getInsurerList({
      ...props.insurerparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setInsurerParams({ ...props.insurerparams, search: val });
    props.getInsurerList({ ...props.insurerparams, search: val });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const data = props.insurerList?.map((row, key) => ({
    ...row,
    index:
      (props.insurerparams.page - 1) * props.insurerparams.page_size +
      (key + 1),
  }));

  const columns = [
    {
      title: "S.No",
      width: 70,
      dataIndex: "index",
      fixed: isMobile ? null : "left",
    },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: 100,
      fixed: isMobile ? null : "left",
    },
    {
      title: "Date",
      width: 100,
      render: (row) => <span className={columnClass}>{row.date}</span>,
    },
    {
      title: "Customer Name",
      width: 100,
      render: (row) => <span className={columnClass}>{row.customername}</span>,
    },
    {
      title: "Customer Mobile",
      width: 110,
      render: (row) => (
        <span className={columnClass}>{row.customermobile}</span>
      ),
    },
    {
      title: "Aadhar Number",
      width: 130,
      render: (row) => <span className={columnClass}>{row.aadharno}</span>,
    },
    {
      title: "Counter Name",
      width: 120,
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
    },
    {
      title: "Vehicle Number",
      width: 120,
      render: (row) => (
        <span className={columnClass}>{row.vehicle?.vehicleno}</span>
      ),
    },
    {
      title: "Customer Amount",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.customeramount}</span>
      ),
    },
    {
      title: "Requested Amount",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.requestedamount}</span>
      ),
    },
    {
      title: "Insurance Category",
      width: 100,
      render: (row) => (
        <span className={columnClass}>{row.insurancecategory_name}</span>
      ),
    },
    {
      title: "Amount",
      width: 100,
      render: (row) => <span className={columnClass}>{row.amount}</span>,
    },
    {
      title: "Actions",
      key: "action",
      width: 100,
      fixed: isMobile ? null : "right",
      render: (row, record) => (
        <Space size="middle">
          <>
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getInsuranceById(row.id);
                props.isInsuranceViewModelVisible(true);
              }}
            ></i>

            {props.userAccessList?.indexOf(
              "InsuranceRto.agent_view_insurance"
            ) !== -1 && (
              <>
                {(row.insurancestatus == 2 || row.insurancestatus == 3) && (
                  <>
                    <i
                      className="ri-add-box-line hp-mr-2 "
                      style={{ cursor: "pointer", color: "#fc6f03" }}
                      onClick={() => {
                        props.getInsuranceById(row.id);
                        props.isInsurerAddModelVisible(true);
                        // props.getInsurerData(row, props.agentInsurerData);
                      }}
                    ></i>
                  </>
                )}
              </>
            )}
            {/* {row.insurancestatus == 1 && (
              <>
                <i
                  className="ri-add-box-line hp-mr-2 "
                  style={{ cursor: "pointer", color: "#fc6f03" }}
                  onClick={() => {
                    props.addInsurerData(row.id);
                    props.isInsurerAddModelVisible(true);
                  }}></i>

              </>)} */}
          </>
        </Space>
      ),
    },
  ];

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Insurance Agent" goBack={false} />
      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          <Col  span={24} xs={18} sm={18} md={14} className="hp-ecommerce-app-header-search ">
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getInsurerList({
                    ...props.insurerparams,
                    page: 1,
                    search: "",
                  });
                  props.setInsurerParams({
                    ...props.insurerparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          <Col span={24} xs={6} sm={6} md={3}>
            <Button
              onClick={() => {
                props.isDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-12"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
               {!isMobile && "Filter"}
            </Button>
          </Col>
        </Row>
      </Col>
      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  rowClassName="hp-btn-bg-color-none"
                  dataSource={data}
                  scroll={{ x: 1300 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                total={props.insurerListCount}
                current={props.insurerparams?.page}
                pageSize={props.insurerparams?.page_size}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <InsuranceViewModel />
      <AddInsurer />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ newInsurance, system }) {
  return {
    insurerList: newInsurance.insurerList,
    insurerparams: newInsurance.insurerparams,
    noofpages: newInsurance.noofpages,
    insurerListCount: newInsurance.insurerListCount,
    loading: newInsurance.loading,
    agentInsurerData: newInsurance.agentInsurerData,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getInsurerList,
  setInsurerParams,
  isInsurerAddModelVisible,
  isDrawerVisible,
  isInsuranceViewModelVisible,
  getInsuranceById,
  addInsurerData,
  getInsurerData,
  clearInsurerList
})(insurer);
